import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { alertErro } from '../layout/modalAlertActions';
import { clearState } from '../../ducks/pages';
import { Button, FormGroup, Label, Input } from 'reactstrap';

class AgendamentoForm extends React.Component {

	constructor(props) {
		super(props);

		this.handleChange = this.handleChange.bind(this);
		this.addAgendamento = this.addAgendamento.bind(this);

		this.state = {
			numero: '',
			disabled: false
		};
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.numero) {
			this.setState({numero: this.props.match.params.numero});
			this.addAgendamento();
		}
	}

	handleChange(event) {
		const { target } = event;
		this.setState({[target.name]: target.value});
	}

	addAgendamento(event) {
		if (event) {
			event.preventDefault();
		}
		this.setState({disabled: true});

		axiosAuth.get(`agendamento/realiza/${this.state.numero || this.props.match.params.numero}`)
			.then(response => {
				if (response.data.status === false) {
					this.props.alertErro(response.data.message);
				} else {
					this.props.clearState('agendamentos');
					alert('Agendamento cadastrado com sucesso!');
				}
				this.setState({disabled: false, numero: ''});
			})
			.catch(err => {
				alert('Ocorreu um erro na comunicao com o servidor. Por favor, tente novamente!');	
				this.setState({disabled: false});
			});
	}

	render() {
		return (
			<form onSubmit={this.addAgendamento}>
				<FormGroup>
					<Label for="numero">Nmero do agendamento</Label>
					<Input type="text" name="numero" id="numero" value={this.state.numero} onChange={this.handleChange} required disabled={this.state.disabled} />
				</FormGroup>
				<Button type="submit" outline color="primary" disabled={this.state.disabled}>Salvar</Button>
			</form>
		);
	}
}

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		alertErro,
		clearState
	}, dispatch)
);

export default connect(null, mapDispatchToProps)(AgendamentoForm);


