import React from 'react';
import { NavLink } from 'react-router-dom';
import FontAwesomeIcon from '@fortawesome/react-fontawesome';

class Sidebar extends React.Component {

	toggleMenu() {
		document.querySelector('.sidebar').classList.toggle('show-menu');
	}

	render(){
		return (
			<nav className="col-md-3 col-xl-2 sidebar">
				<div className="sidebar-sticky">
					<div className="header-nav">
						<h1 className="logo"><a href="/"><img src="../logo-white.png" alt="Consulta Fcil" /></a></h1>
						<button onClick={this.toggleMenu} className="btn-nav">
							<span className="icon-bar top-bar"></span>
							<span className="icon-bar middle-bar"></span>
							<span className="icon-bar bottom-bar"></span>
						</button>
					</div>
					{
						(this.props.ulvl < 4) ? '' :
							<ul className="nav flex-column">
								<li className="nav-item">
									<NavLink className="nav-link" activeClassName="active" to="/agendamento">
										<FontAwesomeIcon icon="file" fixedWidth /> <span>Agendamentos</span>
									</NavLink>
									<ul className="nav-sub">
										<li className="nav-item">
											<NavLink className="nav-link" activeClassName="active" exact to="/agendamento">Listar</NavLink>
										</li>
										<li className="nav-item">
											<NavLink className="nav-link" activeClassName="active" exact to="/agendamento/adicionar">Adicionar</NavLink>
										</li>
									</ul>
								</li>
								<li className="nav-item">
									<NavLink className="nav-link" activeClassName="active" to="/financeiro">
										<FontAwesomeIcon icon="dollar-sign" fixedWidth /> <span>Financeiro</span>
									</NavLink>
									<ul className="nav-sub">
										<li className="nav-item">
											<NavLink className="nav-link" activeClassName="active" exact to="/financeiro">Listar</NavLink>
										</li>
									</ul>
								</li>
								<li className="nav-item">
									<NavLink className="nav-link" activeClassName="active" to="/procedimento">
										<FontAwesomeIcon icon="notes-medical" fixedWidth /> <span>Procedimentos</span>
									</NavLink>
									<ul className="nav-sub">
										<li className="nav-item">
											<NavLink className="nav-link" activeClassName="active" exact to="/procedimento">Listar</NavLink>
										</li>
									</ul>
								</li>
							</ul>
					}
					<div className="nav-bottom d-flex">
						<NavLink className="nav-link" to="/logout" title="Sair">
							<FontAwesomeIcon icon="sign-out-alt" transform="grow-5" />
						</NavLink>
						<NavLink className="nav-link" to="/meus-dados" title="Meus dados">
							<FontAwesomeIcon icon="user-circle" transform="grow-5" /> {localStorage.getItem('email')}
						</NavLink>
					</div>
				</div>
			</nav>
		);
	}
}

export default Sidebar;


