import React from 'react';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Load from '../layout/load';

class AgendamentoSingle extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			codigo: '',
			total: '',
			data_criacao: '',
			cliente: {},
			parceiro: {
				endereco: {}
			},
			procedimentos: [],
			isLoaded: false
		};
	}

	componentWillMount() {
		if (this.props.match && this.props.match.params.id) {

			axiosAuth.get(`agendamento/${this.props.match.params.id}`)
				.then(response => {
					if (response.data.status === false) {
						window.location.href = '/parceiro/#/agendamento';
					} else {
						this.setState({ isLoaded: true, ...response.data[0] });
					}
				}).catch(err => {
					alert('Ocorreu um erro na comunicao com o servidor. Por favor, tente novamente!');
					this.setState({ isLoaded: true });
				});
		}
	}

	render() {
		const { endereco } = this.state.parceiro;
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<dl className="row">
					<dt className="col-sm-3">Cdigo</dt>
					<dd className="col-sm-9">{this.state.codigo}</dd>
					<dt className="col-sm-3">Data de cadastro</dt>
					<dd className="col-sm-9">{this.state.data_criacao ? new Date(this.state.data_criacao).toLocaleDateString('pt-BR') : ''}</dd>
					<dt className="col-sm-3">Parceiro</dt>
					<dd className="col-sm-9">{this.state.parceiro.nome}</dd>
					<dt className="col-sm-3">Funcionamento</dt>
					<dd className="col-sm-9">{this.state.parceiro.horario_funcionamento}</dd>
					<dt className="col-sm-3">Telefone</dt>
					<dd className="col-sm-9">{this.state.parceiro.telefone}</dd>
					<dt className="col-sm-3">Endereo</dt>
					<dd className="col-sm-9">
						{endereco.logradouro ? `${endereco.logradouro}, ${endereco.numero} - ${endereco.bairro}` : ''}<br/>
						{endereco.complemento}
					</dd>
					<dt className="col-sm-3">Status</dt>
					<dd className="col-sm-9">{this.state.status}</dd>
				</dl>
				<h3>Cliente</h3>
				<dl className="row">
					<dt className="col-sm-3">Matrcula</dt>
					<dd className="col-sm-9">{this.state.cliente.cpf && this.state.cliente.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</dd>
					<dt className="col-sm-3">Nome</dt>
					<dd className="col-sm-9">{this.state.cliente.nome}</dd>
					<dt className="col-sm-3">Telefone</dt>
					<dd className="col-sm-9">{this.state.cliente.telefone}</dd>
				</dl>
				<h3>Procedimentos</h3>
				<table className="table">
					<thead>
						<tr>
							<th>Procedimento</th>
							<th>Qnt.</th>
							<th>Valor Unit.</th>
						</tr>
					</thead>
					<tfoot>
						<tr className="table-primary">
							<td></td>
							<td><b>TOTAL</b></td>
							<td><b>{`R$ ${this.state.total}`}</b></td>
						</tr>
					</tfoot>
					<tbody>
						{
							this.state.procedimentos.map(procedimento => (
								<tr>
									<td>{procedimento.codigo_tuss} - {procedimento.nome}</td>
									<td>{procedimento.quantidade}</td>
									<td>{`R$ ${procedimento.valor_parceiro}`}</td>
								</tr>
							))
						}
					</tbody>
				</table>	
				<FloatButton icon="pencil-alt" title="Editar" />
			</div>
		);
	}
}

export default AgendamentoSingle;


