import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import axiosAuth from '../../helpers/axiosAuth';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';

class ProcedimentoList extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			isLoaded: true
		};
	}

	componentDidMount() {
		if (!this.props.procedimentos.length) {
			this.setState({ isLoaded: false });
			axiosAuth.get(`parceiroprocedimento/${localStorage.getItem('uid')}`)
				.then(response => {
					if (response.data.status !== false) {
						this.props.changeState({ procedimentos: response.data });
					}
					this.setState({isLoaded: true});
				})
				.catch(err => { 
					alert('Ocorreu um erro ao se comunicar com o servidor da aplicao');
				});
		}
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<table className="table">
					<thead>
						<tr>
							<th>Cdigo</th>
							<th>Nome</th>
							<th>Valor Procedimento</th>
							<th>Status</th>
						</tr>
					</thead>
					<tbody>
						{
							this.props.procedimentos && this.props.procedimentos.map(procedimento => 
								<tr key={`proc${procedimento.id_parceiro_procedimento}`} className={`status-${procedimento.status}`}>
									<td>{procedimento.codigo_tuss}</td>
									<td>{procedimento.nome}</td>
									<td>R$ {procedimento.valor_parceiro.replace('.', ',')}</td>
									<td>{['inativo', 'ativo'][procedimento.status]}</td>
								</tr>
							)
						}
					</tbody>
				</table>
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	procedimentos: state.pages.procedimentos
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		changeState,
		clearState
	}, dispatch)
);

export default connect(mapStateToProps, mapDispatchToProps)(ProcedimentoList);


