import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Link } from 'react-router-dom';
import axiosAuth from '../../helpers/axiosAuth';
import FloatButton from '../layout/floatButton';
import Search from '../layout/search';
import Load from '../layout/load';
import { changeState, clearState } from '../../ducks/pages';

class AgendamentoList extends React.Component {

	constructor(props) {
		super(props);

		this.state = {
			searchOptions: [
				{name: 'Nmero', value: 'numero'},
				{name: 'Cliente CPF', value: 'cpf', mask: '999.999.999-99'}
			],
			isLoaded: true,
		};

		this.filter = this.filter.bind(this);
	}

	componentDidMount() {
		if (!this.props.agendamentos.length) {
			this.setState({ isLoaded: false });
			axiosAuth.get('agendamento/parceiro')
				.then(response => {
					if (response.data.status !== false) {
						this.props.changeState({ agendamentos: response.data });
					}
					this.setState({ isLoaded: true });
				})
				.catch(err => {
					alert('Ocorreu um erro na comunicao com o servidor. Por favor, tente novamente!');
					this.setState({ isLoaded: true });
				});
		}
	}

	filter(value, field) {
		const url = value ? `agendamento/${value}/${field}` : 'agendamento';
		this.setState({ isLoaded: false });
		this.props.clearState('agendamentos');
		axiosAuth.get(url)
			.then(response => {
				if (response.data.status !== false) {
					this.props.changeState({ agendamentos: response.data });
				}
				this.setState({ isLoaded: true });
			})
			.catch(err => {
				alert('Ocorreu um erro na comunicao com o servidor. Por favor, tente novamente!');
				this.setState({ isLoaded: true });
			});
	}

	render() {
		return (
			<div>
				{this.state.isLoaded ? '' : <Load />}
				<Search options={this.state.searchOptions} onSearch={this.filter} />
				<div className="table-responsive">
					<table className={`table ${this.state.isLoaded?'':'blur'}`}>
						<thead className="">
							<tr>
								<th>Nmero</th>
								<th>Cliente CPF</th>
								<th>Valor</th>
								<th>Status</th>
							</tr>
						</thead>
						<tbody>
							{
								this.props.agendamentos.map(agendamento => 
									<tr key={`agendamento${agendamento.id_agendamento}`}>
										<td><Link to={`/agendamento/v/${agendamento.id_agendamento}`}>{agendamento.codigo}</Link></td>
										<td><Link to={`/cliente/v/${agendamento.cliente.id_cliente}`}>{agendamento.cliente.cpf.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/, '$1.$2.$3-$4')}</Link></td>
										<td>{`R$ ${agendamento.total_parceiro}`}</td>
										<td>{agendamento.status}</td>
									</tr>
								)
							}
						</tbody>
					</table>
				</div>
				<FloatButton onClick={false} to="/agendamento/adicionar" />
			</div>
		);
	}
}

const mapStateToProps = (state) => ({
	agendamentos: state.pages.agendamentos
});

const mapDispatchToProps = (dispatch) => (
	bindActionCreators({
		changeState,
		clearState
	}, dispatch)
)

export default connect(mapStateToProps, mapDispatchToProps)(AgendamentoList);


