import React from 'react';
import { Label, Input, Row, Col } from 'reactstrap';
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import InputMask from 'react-input-mask';

export default class Search extends React.Component {

	constructor(props) {
		super(props);

		const firstOption = this.props.options[0];

		this.state = {
			InputTag: firstOption.mask ? InputMask : Input,
			inputOptions: firstOption.mask ? {...firstOption, maskChar: firstOption.maskChar || ''} : firstOption,
			value: ''
		};

		this.changeFilter = this.changeFilter.bind(this);
		this.handleChange = this.handleChange.bind(this);
	}

	changeFilter(event) {
		const { value } = event.target;
		const option = this.props.options.find(o => o.value === value);

		if (option.mask) {
			this.setState({
				InputTag: InputMask,
				inputOptions: {...option, maskChar: option.maskChar || ''}
			})
		} else {
			this.setState({
				InputTag: Input,
				inputOptions: option
			});
		}
	}

	handleChange(event) {
		this.setState({value: event.target.value});
	}

	render() {
		const { InputTag } = this.state;
		const { name, value, ...options } = this.state.inputOptions;

		return (
			<div className="search mb-4">
				<Row>
					<Col sm="3">
						<Label for="search_tipo">Filtrar por:</Label>
						<Input type="select" id="search_tipo" name="search_tipo" onChange={this.changeFilter}>
							{
								this.props.options.map((option, key) => 
									<option key={`searchopt${option.value}`} value={option.value}>{option.name}</option>
								)
							}
						</Input>
					</Col>
					<Col>
						<Label>&nbsp;</Label>
						<div className="input-group">
							<InputTag type="text" value={this.state.value} className="form-control" onChange={this.handleChange} placeholder="Digite o filtro..." {...options} />
							<div className="input-group-append">
								<button onClick={ () => this.props.onSearch(this.state.value.replace(/\.|\/|-/g, ''), this.state.inputOptions.value) } className="btn btn-outline-secondary" type="button"><FontAwesomeIcon icon="search" /> Pesquisar</button>
							</div>
						</div>
					</Col>
				</Row>
			</div>
		);
	}
}



